import React, { FC, useState } from 'react';
import Check from '@/assets/Check.svg';
import clsx from 'clsx';

interface CheckboxProps {
	className?: string;
	onClick: () => void;
	isChecked: boolean;
}

const Checkbox: FC<CheckboxProps> = ({ className, onClick, isChecked }) => {
	return (
		<div
			className={clsx(
				'border-[3.5px] w-[30px] h-[30px] rounded-[6px] relative cursor-pointer',
				className
			)}
			onClick={() => onClick()}
		>
			{/* {isChecked && <img src={Check.src} alt='' className='w-[100px] absolute pl-[8px] pb-[8px]' />} */}
		</div>
	);
};

export default Checkbox;
