import Checkbox from '@/components/Checkbox/Checkbox';
import clsx from 'clsx';
import React, { useState } from 'react';

const Goal = () => {
	const [isChecked, setIsChecked] = useState<boolean>(false);

	return (
		<div className='bg-[#F0F0F0] shadow-[0px_4px_4px_0px_#00000040_inset] rounded-[20px] py-[42px] pl-[31px]'>
			<div className='flex'>
				<Checkbox
					isChecked={isChecked}
					onClick={() => {
						setIsChecked(true);
					}}
				/>
				<h2 className={clsx('text-[24px] ml-[9px]', { 'text-[#0000005E]': isChecked })}>
					Разработать «Service Desk — Портал для IT-поддержки»
				</h2>
				<button className='text-[#F3F3F3] bg-[#FF7D51] text-[23px] text-center py-[9px] px-[12px]'>
					Оценить себя
				</button>
			</div>
		</div>
	);
};

export default Goal;
